<template>
  <div>
    <div class="grid-container">
      <div class="grid-item-streaming" id="pgm-streaming" v-if="streaming">
        <span class="highlight" id="pgm-streaming-title"
          >PGM STREAMING
          <span v-if="streaming">({{ stremingtime }})</span></span
        >
      </div>
      <div class="grid-item" v-else>
        <span class="highlight">PGM STREAMING</span>
      </div>
      <div class="grid-item">
        <span class="highlight">PGM MASTER</span>
      </div>
      <div class="grid-item">
        <span class="highlight">ANTENA 10 EDUCA (10.2)</span>
      </div>
      <div class="grid-item">
        <span class="highlight">ANTENA 10 EDUCA II (10.3)</span>
      </div>
      <div class="grid-item">
        <span class="highlight">PGM SWITCHER</span>
      </div>
      <div class="grid-item">
      <span class="highlight">SKYPE01</span>
      </div>
      <div class="grid-item">
        <span class="highlight"></span>
      </div>
      <div class="grid-item">
        <span class="highlight"></span>
      </div>
      <div class="grid-item">
        <span class="highlight">VIVO01</span>
      </div>
      <div class="grid-item">
        <span class="highlight">VIVO02</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH106-A</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH106-B</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH136-A</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH136-B</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH136-C</span>
      </div>
      <div class="grid-item">
        <span class="highlight">MMH136-D</span>
      </div>
    </div>
    <!--
    <div v-else>
      <h1>SETTINGS</h1>
      <div v-for="item in items" :key="item.text">
        <input type="text" v-model="item.text" /> {{ item.text }}
      </div>
      <button v-on:click="persist">Save</button>  
    </div>
    -->
  </div>
</template>

<script>
import OBSWebSocket from "obs-websocket-js";
const obs = new OBSWebSocket();

export default {
  data() {
    return {
      connected: false,
      streaming: false,
      stremingtime: "",
      streamingcolor: "rgba(0, 0, 0, 0.8)",
      /*items: [
        { text: "PGM STREAMING" },
        { text: "PGM MASTER" },
        { text: "PGM SWITCHER" },
        { text: "SKYPE01" },
        { text: "MMH243_LiveU03" },
        { text: "MMH243_LiveU04" },
        { text: "NDICENTRAL01(BOND01)" },
        { text: "NDICENTRAL01(BOND02)" },
        { text: "NDICENTRAL01(BOND03)" },
        { text: "NDICENTRAL01(BOND04)" },
        { text: "NDICENTRAL03(BOND05)" },
        { text: "NDICENTRAL03(BOND06)" },
        { text: "NDICENTRAL03(PARNAIBA)" },
        { text: "NDICENTRAL03(TORRE)" },
      ],*/
    };
  },

  mounted() {
    /*if (localStorage.items) {
      this.items[1] = localStorage.items;
    }*/

    if (document.location.hash != "#settings") {
      this.connect();

      obs.on("AuthenticationSuccess", () => {
        obs.send("GetStreamingStatus").then((data) => {
          if (!data.streaming) {
            document.getElementById("pgm-streaming").style.borderColor =
              "rgba(39, 85, 163, 0.8)";
            document.getElementById("pgm-streaming-title").style.background =
              "rgba(0, 56, 98, 0.8)";
            document.getElementById("pgm-streaming-title").style.color =
              "rgb(252, 251, 250)";
            this.streaming = false;
          } else this.streaming = true;
        });
      });
      
      obs.on("StreamStarted", () => {
        this.streaming = true;
      });

      obs.on("StreamStopped", () => {
        this.streaming = false;
        document.getElementById("pgm-streaming").style.borderColor =
          "rgba(39, 85, 163, 0.8)";
        document.getElementById("pgm-streaming-title").style.background =
          "rgba(0, 56, 98, 0.8)";
        document.getElementById("pgm-streaming-title").style.color =
          "rgb(252, 251, 250)";
      });

      obs.on("StreamStatus", (data) => {
        let red = data.strain * 2.0;
        if (red > 1.0) red = 1.0;
        red *= 255.0;

        let green = (1.0 - data.strain) * 2.0;
        if (green > 1.0) green = 1.0;
        green *= 255.0;

        this.time(data["total-stream-time"]);
        this.streamingcolor = "rgba(" + red + ", " + green + ", 0, 0.8)";

        document.getElementById("pgm-streaming").style.borderColor =
          this.streamingcolor;
        document.getElementById("pgm-streaming-title").style.background =
          this.streamingcolor;
        document.getElementById("pgm-streaming-title").style.color = "black";
      });

      obs.on("ConnectionClosed", () => {
        this.connected = false;
        this.streaming = false;

        document.getElementById("pgm-streaming").style.borderColor =
          "rgba(39, 85, 163, 0.8)";
        document.getElementById("pgm-streaming-title").style.background =
          "rgba(0, 56, 98, 0.8)";
        document.getElementById("pgm-streaming-title").style.color =
          "rgb(252, 251, 250)";
      });

      obs.on("Exiting", () => {
        this.connected = false;
        this.streaming = false;

        document.getElementById("pgm-streaming").style.borderColor =
          "rgba(39, 85, 163, 0.8)";
        document.getElementById("pgm-streaming-title").style.background =
          "rgba(0, 56, 98, 0.8)";
        document.getElementById("pgm-streaming-title").style.color =
          "rgb(252, 251, 250)";
      });
    }
  },

  methods: {
    dopost() {
      if (this.connected) return;

      try {
        obs
          .connect({ address: "192.168.0.116:4444", password: "" })
          .then(() => {
            this.connected = true;
          });
      } catch (error) {
        console.log(error);
      }

      setTimeout(this.dopost, 3000);
    },

    connect() {
      this.connected = false;
      this.streaming = false;

      if (!this.connected) setTimeout(this.dopost, 3000);
    },

    time(toconvert) {
      var date = new Date(0);
      date.setSeconds(toconvert);
      this.stremingtime = date.toISOString().substr(11, 8);
    },

    /*persist() {
      localStorage.items = this.items[1];
    },*/
  },
};
</script>

<style scoped>
.grid-container {
  display: grid;
  grid-template-columns: 480px 480px 480px 480px;
  grid-template-rows: 270px 270px 270px 270px;
}

.grid-item {
  border: 2px solid rgba(39, 85, 163, 0.8);
  padding-top: 239px;
  font-size: 20px;
  color: rgb(252, 251, 250);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
  text-align: center;
}

.grid-item-streaming {
  border: 4px solid rgba(39, 85, 163, 0.8);
  padding-top: 235px;
  font-size: 20px;
  color: rgb(252, 251, 250);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
  text-align: center;
}

.highlight {
  background: rgba(0, 56, 98, 0.8);
}
</style>
